section.section-review {
    padding: 60px 0;

    .section-title {
        font-size:   24px;
        line-height: 24px;
        font-weight: 700;
        color:       #000000;
        text-align:  center;
        background:  0 0;
    }

    .review-list {
        display:        flex;
        flex-direction: column;
        align-items:    center;
        margin-top:     40px;

        .review-item {
            display:        flex;
            flex-direction: column;
            align-items:    center;
            width:          250px;
            margin:         0 0 30px;
            text-align:     center;

            .review-popup {
                position:        relative;
                width:           225px;
                height:          225px;
                border-radius:   50%;
                border-bottom:   none;
                box-shadow:      none;
                text-decoration: none;
                outline:         none;

                &:hover {
                    border-bottom: none;
                    box-shadow:    none;
                }

                &:before {
                    content:          '';
                    position:         absolute;
                    top:              0;
                    left:             0;
                    opacity:          0;
                    width:            100%;
                    height:           100%;
                    border-radius:    50%;
                    background-color: rgba(250, 58, 94, .7);
                    transition:       opacity .3s;
                }

                &:after {
                    content:      '';
                    position:     absolute;
                    top:          50%;
                    left:         50%;
                    opacity:      0;
                    width:        60px;
                    height:       60px;
                    border-color: transparent transparent transparent #ffffff;
                    border-style: solid;
                    border-width: 30px 0 30px 50px;
                    transform:    translate(-40%, -50%);
                    transition:   opacity .3s;
                }

                &:hover {
                    &:before {
                        opacity: 1;
                    }

                    &:after {
                        opacity: 1;
                    }
                }
            }

            .review-title {
                margin-top:  15px;
                font-size:   18px;
                font-weight: 700;
            }

            .review-work {
                margin-top: 5px;
                font-size:  14px;
                color:      #8c8c8c;
            }
        }
    }

    @media (min-width: 620px) {
        .section-title {
            font-size:   28px;
            line-height: 28px;
        }

        .review-list {
            flex-direction:  row;
            justify-content: center;
            align-items:     stretch;
            margin-top:      60px;

            .review-item {
                margin: 0 50px;

                .review-title {
                    flex-grow: 1;
                    font-size: 22px;
                }
            }
        }
    }

    @media (min-width: 768px) {
        padding: 90px 0;

        .section-title {
            font-size:   36px;
            line-height: 36px;
        }

        .review-list {
            .review-item {
                .review-title {
                    margin-top: 20px;
                    font-size:  26px;
                }

                .review-work {
                    margin-top: 20px;
                    font-size:  16px;
                }
            }
        }
    }
}